.match-player {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: relative;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

.match-player .choose-player, .match-player .selected-player {
    width: 100%;
    position: relative;

    -webkit-transform-style: preserve-3d;

            transform-style: preserve-3d;
    -webkit-perspective-origin: top center;
            perspective-origin: top center;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
    overflow: hidden;
}

.match-player .choose-player {
    z-index: 2;
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
    -webkit-animation-name: show;
            animation-name: show;
}

.match-player.player-is-selected .choose-player {
    -webkit-animation-name: no-show;
            animation-name: no-show;
}

.match-player .selected-player {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
    -webkit-animation-name: no-show;
            animation-name: no-show;
}

.match-player.player-is-selected .selected-player {
    -webkit-animation-name: show;
            animation-name: show;
}

.match-player button {
    margin: 16px;
    color: #7b7b7b;
    background-color: transparent;
    border-radius: 64px;
    border-color: #747474;
    padding: 0 2.6rem;
    height: 3.6rem;
    font-size: 1.4rem;
    font-weight: 500;
    outline: none;
    /* clean-css ignore:start */
    width: calc(100% - 32px);
    /* clean-css ignore:end */
}

.selected-player {
    font-size: 2.3rem;
}

.selected-player .card {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.selected-player .card-header {
    background: #EC1164;
    color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    text-transform: uppercase;
    font-size: 2.2rem;
    font-weight: 400;
    margin: 0;
}

.selected-player.is-winning .card-header {
    background: #5EC0D5;
}

.selected-player.is-winning img {
    border-color: #5EC0D5;
}

.selected-player img {
    border-radius: 100%;
    max-width: 150px;
    margin: 20px auto;
    border: 3px solid #EC1164;
    width: 100%;
}

.selected-player p {
    text-align: center;
    margin: 10px auto;
}

.selected-player hr {
    margin: 10px 10%;
    background: #747474;
    border: 1px solid #747474;
    height: 1px;
}

.selected-player dl {
    padding: 0 25px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.selected-player dl * {
    width: 80%;
    margin: 0 0 20px 0;
}

.selected-player dl dd {
    text-align: right;
    width: 20%;
}

@-webkit-keyframes no-show {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        height: auto;
        width: 100%;
    }

    49% {
        height: auto;
        width: 100%;
    }

    50% {
        height: 0;
        width: 0;
    }

    100% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
        height: 0;
        width: 0;
    }
}

@keyframes no-show {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        height: auto;
        width: 100%;
    }

    49% {
        height: auto;
        width: 100%;
    }

    50% {
        height: 0;
        width: 0;
    }

    100% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
        height: 0;
        width: 0;
    }
}

@-webkit-keyframes show {
    0% {
        -webkit-transform: rotateY(-180deg);
                transform: rotateY(-180deg);
        height: 0;
        width: 0;
    }

    49% {
        height: 0;
        width: 0;
    }

    50% {
        height: auto;
        width: 100%;
    }

    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        height: auto;
        width: 100%;
    }
}

@keyframes show {
    0% {
        -webkit-transform: rotateY(-180deg);
                transform: rotateY(-180deg);
        height: 0;
        width: 0;
    }

    49% {
        height: 0;
        width: 0;
    }

    50% {
        height: auto;
        width: 100%;
    }

    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        height: auto;
        width: 100%;
    }
}



